<template>
  <div class="listSearch">
    <van-nav-bar
      title="商品列表"
      left-text="返回"
      left-arrow
      @click-left="goBack"
      fixed
    />
    <div class="product-list clearfix">
      <div
        class="fl product-img-box"
        v-for="(item, index) in searchData"
        :key="index"
         @click="GoDetail(item.goods_id)"
      >
        <img class="auto-img" :src="item.goods_big_logo" />
        <div class="product-text-box">
          <div class="product-title-box clearfix">
            <div class="product-Special-price fl" v-if="item.isDiscount">
              特卖价
            </div>
            <div class="product-price fl">￥{{ item.goods_price }}</div>
            <div class="product-discount fl" v-if="item.isDiscount">
              {{ item.discountPrice }}折
            </div>
          </div>
          <div class="product-name tow-text">
            {{ item.goods_name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "../assets/less/listsearch.less";
export default {
  name: "ListSearch",
  data() {
    return {
      searchData: [],
      cid: "",
    };
  },
  created() {
    // 截取商品cid
    this.cid = this.$route.params.cid;
    // 获取商品数据
    this.getProductData();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 获取商品列表数据
    getProductData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/search",
        params: {
          cid: this.cid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          let data = result.data.message.goods;
          data.map((v, i) => {
            if (v.goods_big_logo === "") {
              v.goods_big_logo = 'http://image5.suning.cn/uimg/b2c/newcatentries/0000000000-000000000160455569_1_800x800.jpg';
              v.goods_name = '海信(Hisense)LED55MU9600X3DUC 55英寸 4K超高清量子点电视 ULED画质 VIDAA系统';
            }
            if (v.goods_price == 0) {
              v.goods_price = 1888;
            }
            v.isDiscount = false;
            if (v.goods_price % 2 != 0) {
              v.isDiscount = true;
              v.discountPrice = `${Math.ceil(Math.random() * 9)}.${Math.ceil(
                Math.random() * 9
              )}`;
            }
          });

          this.searchData = result.data.message.goods
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    GoHome() {
      this.$router.push({ name: "Home" });
    },
    GoDetail(goods_id) {
      this.$router.push({ name: "Detail",params:{goods_id} });
    },
  },
};
</script>
